	//--------------------- SCROLL ANCORS ---------------------//
	const scrollToHref = function () {
		let pageURL = location.href;
		let urlToObject = '#' + pageURL.split("#")[1] + '';
		// scrollAnchors(e, urlToObject)

		const distanceToTop = el => Math.floor(el.getBoundingClientRect().top);
		const targetAnchor = document.querySelector(urlToObject);
		if (!targetAnchor) return;
		const originalTop = distanceToTop(targetAnchor) - document.querySelector('header .headerContent').clientHeight;
		// console.log(originalTop);
		window.scrollBy({ top: originalTop, left: 0, behavior: 'smooth' });

	}

	const scrollAnchors = function (e, respond = null) {

		const distanceToTop = el => Math.floor(el.getBoundingClientRect().top);

		// console.log(e);

		e.preventDefault();

		var targetID = (respond) ? respond.getAttribute('href') : this.getAttribute('href');

		const targetAnchor = document.querySelector(targetID);
		// console.log(targetAnchor);
		
		if (!targetAnchor) return;
		const originalTop = distanceToTop(targetAnchor) - document.querySelector('header .headerContent').clientHeight;
		
		window.scrollBy({ top: originalTop, left: 0, behavior: 'smooth' });

		const checkIfDone = setInterval(function () {
			const atBottom = window.innerHeight + window.pageYOffset >= document.body.offsetHeight - 2;

			if (distanceToTop(targetAnchor) === 0 || atBottom) {
				targetAnchor.tabIndex = '-1';
				targetAnchor.focus();
				window.history.pushState('', '', targetID);
				clearInterval(checkIfDone);
			}
		}, 500);

	}

	//--------------------- DETECT BROWSER ---------------------//
	const detectBrowser = function () {

		navigator.sayswho = (function () {
			let ua = navigator.userAgent, tem,
				M = ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];

			if (/trident/i.test(M[1])) {
				tem = /\brv[ :]+(\d+)/g.exec(ua) || [];
				return 'IE ' + (tem[1] || '');
			}

			if (M[1] === 'Chrome') {
				tem = ua.match(/\b(OPR|Edge)\/(\d+)/);
				if (tem != null) return tem.slice(1).join(' ').replace('OPR', 'Opera');
			}

			M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
			if ((tem = ua.match(/version\/(\d+)/i)) != null) M.splice(1, 1, tem[1]);

			return M;
		})();

		// console.log(navigator.sayswho);
		// console.log(navigator.userAgent);

		if (typeof navigator.sayswho === 'string') {
			let sayWho = navigator.sayswho.split(' ');

			if (sayWho[0] == 'IE' && sayWho[1] <= '11') {
				// console.warn('ACTUALICE EL NAVEGADOR');

				setTimeout(function () {
					document.querySelector('.blackboxLoader').style.display = 'none';
					document.querySelector('.blackboxBrowser').style.display = 'block';
				}, 1500);

			}
			else {
				// console.warn('DONT WORRY');

			}

		}
		else if (navigator.sayswho[0] == 'IE' && navigator.sayswho[1] <= '11') {
			// console.warn('ACTUALICE EL NAVEGADOR');

			setTimeout(function () {
				document.querySelector('.blackboxLoader').style.display = 'none';
				document.querySelector('.blackboxBrowser').style.display = 'block';
			}, 1500);

		}
		else {
			// console.warn('DONT WORRY');

			

		}

	}

	//--------------------- CAMBIOS AL HACER SCROLL ---------------------//
	const changesScroll = function () {
		const numberOfActions = 0;

		window.onscroll = function () {
			let scrollTop = document.documentElement.scrollTop;
			let header = document.querySelector('header');
			let heightHeader = document.querySelector('header').clientHeight;

			if (document.getElementById('btnMenuMobile')) {
				if (document.getElementById('btnMenuMobile').classList.contains('btnMenuMobileOpen')) {
					document.getElementById('btnMenuMobile').click();
				}
			}

			// ADD STICKY MENU
			if (scrollTop > header.querySelector('.topHeader').clientHeight) {
				document.getElementById('mainContent').classList.add('contentStiky');
				header.classList.add('headerStiky');
				TweenMax.to(document.querySelector('.fixedRedes'), 0.2, { display: 'block', autoAlpha: 1 });
				
			}
			else if (scrollTop < header.querySelector('.topHeader').clientHeight) {
				document.getElementById('mainContent').classList.remove('contentStiky');
				header.classList.remove('headerStiky');
				TweenMax.to(document.querySelector('.fixedRedes'), 0.2, { autoAlpha: 0, display: 'none' });
			}
			
			// SHOW BTN TOP SCROLL
			if (scrollTop > window.innerHeight / 3) {
				TweenMax.to(document.querySelector('.btnScrolTop'), 0.2, { display: 'block', autoAlpha: 1 });	
			}
			else {
				TweenMax.to(document.querySelector('.btnScrolTop'), 0.2, { autoAlpha: 0, display: 'none' });
			}

			// CHECK SCROLL FROM THE BOTTOM
			if (scrollTop + window.innerHeight > document.body.clientHeight - 50) {
				TweenMax.to(document.querySelector('.fixedRedes'), 0.2, { autoAlpha: 0, display: 'none' });
			}
			else if (scrollTop + window.innerHeight > document.body.clientHeight - 50){
				TweenMax.to(document.querySelector('.fixedRedes'), 0.2, { display: 'block', autoAlpha: 1 });
			}

		};
	};

