	//--------------------- FUNCIONALIDAD DROPDOWN ---------------------//
	const funcionalidadDropdownDesktop = function () {

		const btnDropDown = document.querySelectorAll('.dropdownMenu');

		// console.log(btnDropDown);

		[].forEach.call(btnDropDown, btn => {
			btn.addEventListener('mouseover', btnOver, false);
			btn.addEventListener('mouseout', btnOut, false);
		})

		function btnOver() {

			this.classList.add('openDropdown');

			[].forEach.call(btnDropDown, btn => {

				let fadeInfo = btn.querySelector('ul.subMenu');
				let btnSubMenu = fadeInfo.getElementsByTagName('li');
				let liSubQty = btnSubMenu.length;
				let maxHeightBtns = [];

				for (let i = 0; i < btnSubMenu.length; i++) {
					const currentBtn = btnSubMenu[i];
					maxHeightBtns.push(currentBtn.clientHeight);
				}
				

				if (btn !== this) {

					btn.classList.remove('openDropdown');

				}

				if (btn.classList.contains('openDropdown')) {

					TweenMax.to(fadeInfo, 0.4, { y: 0, height: (Math.max(...maxHeightBtns) * liSubQty), autoAlpha: 1, ease: Power1.easeOut });

				}

			});

		}

		function btnOut() {

			this.classList.remove('openDropdown');

			[].forEach.call(btnDropDown, btn => {

				let fadeInfo = btn.querySelectorAll('ul.subMenu');

				if (btn !== this) {

					btn.classList.remove('openDropdown');

				}

				if (!btn.classList.contains('openDropdown')) {

					TweenMax.to(fadeInfo, 0.4, { y: -5, height: 0, autoAlpha: 0, ease: Power1.easeOut });

				} else {

					// subMenu.style.maxHeight = '0px';

				}

			});


		}

	};

	const funcionalidadDropdownMobile = function () {

		const btnDropDown = document.querySelectorAll('.dropdownMenu');

		// console.log(btnDropDown);

		[].forEach.call(btnDropDown, btn => {
			btn.querySelector('a.btnSubMenu').addEventListener('click', btnClick, false);
		})

		function btnClick() {
			this.parentElement.classList.toggle('openDropdown');
			// this.style.height = '11%';

			[].forEach.call(btnDropDown, btn => {

				let fadeInfo = btn.querySelector('ul.subMenu');
				let subMenu = fadeInfo.getElementsByTagName('li');
				let liSubQty = subMenu.length;
				let maxHeightBtns = [];

				for (let i = 0; i < subMenu.length; i++) {
					const currentBtn = subMenu[i];
					maxHeightBtns.push(currentBtn.clientHeight);
				}

				if (btn !== this.parentElement) {

					btn.classList.remove('openDropdown');

				}

				if (btn.classList.contains('openDropdown')) {

					TweenMax.to(fadeInfo, 0.4, { height: (Math.max(...maxHeightBtns) * liSubQty), ease: Power1.easeOut });

				}
				else {

					TweenMax.to(fadeInfo, 0.4, { height: 0, ease: Power1.easeOut });

				}

			});


		}


	};

