	//--------------------- ANIMACIÓN DE CONTEO NUMÉRICO ---------------------//
	const counterNumber = function (obj, start, end) {
		var range = end - start;
		var current = start;
		var increment = end > start ? 1 : -1;
		var stepTime = Math.abs(Math.floor(1200 / range));
		// var obj = document.querySelectorAll(obj);
		var timer = setInterval(function () {
			current += increment;
			obj.innerHTML = current;
			if (current == end) {
				clearInterval(timer);
			}
		}, stepTime);
	}

